import React, { Component } from 'react';
import { Button, ButtonGroup, Container, Table } from 'reactstrap';
import AppNavbar from './AppNavbar';
import { Link } from 'react-router-dom';

class EmployeeList extends Component {

    constructor(props) {
        super(props);
        this.state = {employee: []};
        this.remove = this.remove.bind(this);
    }

    componentDidMount() {
        const requestOptions = {
            method: 'GET',
            headers: {
                accept: 'application/json'
                }
        };
        console.log(requestOptions);
        try {
            fetch('http://localhost:8080/employee/getAll',requestOptions)
                .then(response => response.json())
                .then(data => this.setState({employee: data}));
        }
        catch(err) {
            console.log(err.toString())
        }
    }
    async remove(id) {
        await fetch(`/employee/${id}`, {
            method: 'DELETE',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            }
        }).then(() => {
            let updatedEmployee = [...this.state.employee].filter(i => i.id !== id);
            this.setState({employee: updatedEmployee});
        });
    }

    render() {
        const {employee} = this.state;
        const employeeList = employee.map(employee => {
            return <tr key={employee.idNhanVien}>
                <td>{employee.ten}</td>
                <td style={{whiteSpace: 'nowrap'}}>{employee.diaChi}</td>
                <td>{employee.sdt}</td>
                <td>
                    <ButtonGroup>
                        <Button size="sm" color="primary" tag={Link} to={"/employee/" + employee.idNhanVien}>Edit</Button>
                        <Button size="sm" color="danger" onClick={() => this.remove(employee.idNhanVien)}>Delete</Button>
                    </ButtonGroup>
                </td>
            </tr>
        });

        return (
            <div>
                <AppNavbar/>
                <Container fluid>
                    <div className="float-right">
                        <Button color="success" tag={Link} to="/employee/new">Add employee</Button>
                    </div>
                    <h3>employee</h3>
                    <Table className="mt-4">
                        <thead>
                        <tr>
                            <th width="30%">Name</th>
                            <th width="30%">Email</th>
                            <th width="40%">Actions</th>
                        </tr>
                        </thead>
                        <tbody>
                        {employeeList}
                        </tbody>
                    </Table>
                </Container>
            </div>
        );
    }
}

export default EmployeeList;